import { Component, OnInit, Input } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { Http, Response } from '@angular/http';
import { DashboardComponent } from '../dashboard/dashboard.component';
import { IpInfo } from '../ip-info';

@Component({
  selector: 'app-chart',
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.css'],
  providers: [IpInfo]
})

export class ChartComponent implements OnInit {
  connectURL = this.IpInfo.ipDetail;
  url = this.connectURL + 'modality?fetchAll=true&limit=16000&question=2';
  findDataTrend = [];
  ibCount = [];
  mapRegion = [];
  modality = [];
  region = [];
  val = [];
  chart1;
  chart2;
  chart3;
  constructor(private http: Http,
    private DashboardComponent: DashboardComponent, private IpInfo: IpInfo) {
  }

  ngOnInit() {
    let mapCount = [];
    const reducer = (accumulator, currentValue) => accumulator + currentValue;
    const apiConnectionString = this.connectURL + 'modality?fetchAll=true&limit=16000&question=2';
    this.http
      .get(apiConnectionString)
      .map(compResponse => compResponse.json())
      .subscribe(result => {
        mapCount = result.data;
        mapCount.map(i => i.region).filter((i, index, arr) => arr.indexOf(i) === index).map(i => {
          this.mapRegion.push(mapCount.filter(a => a.region === i).map(x => x.ibCount / 1000).reduce(reducer));
          this.mapRegion = this.mapRegion.map(i => Math.round(i));
          this.region.push(i);
        });
        mapCount.map(i => i.systemCoverageLevelWarrantyc).filter((i, index, arr) => arr.indexOf(i) === index).map(i => {
          this.ibCount.push(mapCount.filter(a => a.systemCoverageLevelWarrantyc === i).map(x => x.ibCount).reduce(reducer));
          this.modality.push(i);
        });
        mapCount.map(i => i.modality).filter((i, index, arr) => arr.indexOf(i) === index).map(i => {
          this.findDataTrend.push({
            name: i,
            y: mapCount.filter(a => a.modality === i).map(x => x.ibCount / 1000).reduce(reducer)
          });
        });
        this.loadChart();
      },
        error => {
          console.log('Cannot connecct to API');
        },
    );
  }

  funct(fromParam) {
    this.DashboardComponent.sendQuery(fromParam, 'fromChat');
  }

  loadChart() {
    this.chart1 = new Chart({
      credits: {
        enabled: false
      },
      colors: ['#006bb3', '#343537', '#e68a00'],
      chart: {
        width: 400,
        height: 370,
        backgroundColor: '#223440',
        type: 'area',
        style: {
          fontFamily: 'Ge Inspira'
        },
      },
      title: {
        style: {
          color: 'white',
          fontSize: '15',
          fontWeight: 'bold'
        },
        text: 'IB Count based on System Coverage Level'
      },
      xAxis: {
        categories: this.modality,
        labels: {
          style: {
            color: 'white'
          }
        },
        crosshair: true,
        title: {
          style: {
            color: 'white',
            fontSize: '15',
            fontWeight: '500'
          },
          text: 'System Coverage Levels'
        }
      }, legend: {
        enabled: false,
        itemStyle: {
          color: 'white',
          fontSize: '14',
          fontWeight: '500'
        },
        itemHoverStyle: {
          color: '#869aa4',
          fontSize: '14',
          fontWeight: '500'
        },
      },
      yAxis: {
        visible: false,
        gridLineWidth: 0,
        minorGridLineWidth: 0,
        min: 0,
        labels: {
          style: {
            color: 'white'
          },
          formatter: function () {
            if (this.value.toFixed(0) >= 1000) {
              return '' + this.value.toFixed(0) / 10 + 'K';
            } else {
              return '' + this.value.toFixed(0);
            }
          }
        },
        title: {
          style: {
            color: 'white',
            fontSize: '15',
            fontWeight: '500'
          },
          text: 'IB Count'
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px;">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.0f}K</b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        bar: {
          pointPadding: 0.2,
          pointWidth: 20,
          borderWidth: 0,
          dataLabels: {
            style: {
              fontSize: '9',
              color: '#cad1d6',
              textOutline: 'none',
              fontWeight: '300'
            },
            enabled: true,
            crop: false,
            format: '{y}K',
            overflow: 'none'
          }
        },
        series: {
          cursor: 'pointer',
          events: {
            click: (event) => {
              let fromParam = 'ib Count ' + event.point.category;
              this.funct(fromParam);
            }
          }
        }
      },
      series: [{
        name: 'IB Count',
        data: this.ibCount.map(i => Math.round(i / 1000))
      }],
    });

    this.chart2 = new Chart({
      credits: {
        enabled: false
      },
      colors: ['#fd9057de'],
      chart: {
        width: 400,
        height: 370,
        backgroundColor: '#223440',
        type: 'column',
        style: {
          fontFamily: 'Ge Inspira'
        },
      },
      title: {
        style: {
          color: 'white',
          fontSize: '15',
          fontWeight: 'bold'
        },
        text: 'IB Count based on Region'
      }, legend: {
        enabled: false
      },
      xAxis: {
        categories: this.region,
        crosshair: true,
        labels: {
          style: {
            color: 'white'
          }
        },
        title: {
          style: {
            color: 'white',
            fontSize: '15',
            fontWeight: '500'
          },
          text: 'Region'
        }
      },
      yAxis: {
        visible: false,
        gridLineWidth: 0,
        minorGridLineWidth: 0,
        min: 0,
        labels: {
          style: {
            color: 'white'
          },
          formatter: function () {
            if (this.value.toFixed(0) >= 1000) {
              return '' + this.value.toFixed(0) / 10 + 'K';
            } else {
              return '' + this.value.toFixed(0);
            }
          }
        },
        title: {
          style: {
            color: 'white',
            fontSize: '15',
            fontWeight: '500'
          },
          text: 'IB Count'
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.0f} K</b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        column: {
          pointWidth: 28,
          pointPadding: 0.2,
          borderWidth: 0,
          dataLabels: {
            style: {
              fontSize: '9',
              color: '#cad1d6',
              textOutline: 'none',
              fontWeight: '300'
            },
            enabled: true,
            crop: false,
            format: '{y}K',
            overflow: 'none'
          }
        },
        series: {
          cursor: 'pointer',
          events: {
            click: (event) => {
              let fromParam = 'ib Count ' + event.point.category;
              this.funct(fromParam);
            }
          }
        }
      },
      series: [{
        name: 'IB Count',
        data: this.mapRegion
      }]
    });
    this.chart3 = new Chart({
      credits: {
        enabled: false
      },
      colors: ['#C9C100', '#9555EE', '#006bb3', '#ED6116', '#727270', '#D83B40', '#6C7DFF', '#D52659'],
      chart: {
        width: 420,
        height: 320,
        backgroundColor: '#223440',
        type: 'pie'
      },
      title: {
        style:
          {
            color: 'white',
            fontSize: '15',
            fontWeight: 'bold',
            fontFamily: 'Ge Inspira'
          },
        text: 'IB Count based on Modality'
      },
      xAxis: {
        categories: ['LATAM', 'USCAN', 'Europe', 'China', 'APAC'],
        crosshair: true,
        title: {
          style: {
            color: 'white',
            fontSize: '15',
            fontWeight: '500'
          },
          text: 'Modality'
        }
      },
      yAxis: {
        min: 0,
        labels: {
          formatter: function () {
            if (this.value.toFixed(0) >= 1000) {
              return '' + this.value.toFixed(0) / 10 + 'K';
            } else {
              return '' + this.value.toFixed(0);
            }
          }
        },
        title: {
          style: {
            color: 'white',
            fontSize: '15',
            fontWeight: '500'
          },
          text: 'IB Count'
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px;">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.0f}K</b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        pie: {
          borderWidth: 0,
          dataLabels: {
            style: {
              fontSize: '9',
              color: 'white',
              textOutline: 'none',
              fontWeight: '300'
            },
            enabled: true,
            crop: false,
            overflow: 'none'
          }
        },
        series: {
          cursor: 'pointer',
          events: {
            click: (event) => {
              let fromParam = 'ib Count ' + event.point.name;
              this.funct(fromParam);
            }
          }
        }
      },
      series: [{
        name: 'IB Count',
        data: this.findDataTrend
      }]
    });
  }
}



