import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/observable/of';

@Injectable()
export class LuisQueryService {

  constructor(private http: Http) { }

  sendQuery(fromUser) {
    console.log('connecting to LUIS. . .');
    const url = 'https://westus.api.cognitive.microsoft.com/luis/v2.0/apps/3c21ee99-da89-4685-b3a5-269ca41cdd4f?subscription-key=03f4f66e11204a678ffe4bc0a10ece78&verbose=true&timezoneOffset=0&q=';
    return this.http
      .get(url + fromUser)
      .map(botResponse => botResponse.json());
  }
}



