import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {

  transform(array: Array<any>, orderField: string, orderType: boolean): Array<string> {
    array.sort((a: any, b: any) => {
      let ae = new Date(Date.parse(a[orderField]));
      let be = new Date(Date.parse(b[orderField]));
      if (ae === undefined && be == undefined) {
        return 0;
      } else if (ae === undefined && be !== undefined) {
        return orderType ? 1 : -1;
      } else if (ae !== undefined && be === undefined) {
        return orderType ? -1 : 1;
      } else if (ae === be) { return 0; }
      return orderType ? (ae.getTime() > be.getTime() ? -1 : 1) : (be.getTime() > ae.getTime() ? -1 : 1);
    });
    return array;
  }
}


