import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { AppComponent } from './app.component';

import { AppRoutingModule } from './app-routing.module';
import { ChartModule } from 'angular-highcharts';
import { ChartComponent } from './chart/chart.component';

import { PaginationService } from './services/pagination.service';
import { ConnectApiService } from '../app/services/connect-api.service';
import { LuisQueryService } from '../app/services/luis-query.service';
import { DashboardComponent } from './dashboard/dashboard.component';
import { IdbService } from '../app/services/i-db.service';
import { TimeagoPipe } from '../app/shared/pipe/timeago.pipe';
import { NoCommaPipe } from '../app/shared/pipe/no-comma.pipe';
import { HighlightDirective } from './directive/highlight.directive';
import { SortPipe } from './shared/pipe/sort.pipe';
import { IpInfo } from './ip-info';


@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    HttpModule,
    AppRoutingModule,
    ChartModule
  ],
  declarations: [
    TimeagoPipe,
    NoCommaPipe,
    AppComponent,
    ChartComponent,
    DashboardComponent,
    HighlightDirective,
    SortPipe
  ],
  providers: [PaginationService,
    ConnectApiService,
    LuisQueryService,
    IdbService,
    IpInfo],
  bootstrap: [AppComponent]
})

export class AppModule { }



