import { Injectable } from '@angular/core';
import { AngularIndexedDB } from 'angular2-indexeddb';
@Injectable()
export class IdbService {

  db = new AngularIndexedDB('digitalTwin', 1);

  constructor() {

    this.db.openDatabase(1, (evt) => {
      let objectStore = evt.currentTarget.result.createObjectStore(
        'questions', { keyPath: 'id', autoIncrement: true });
      objectStore.createIndex('question', 'question', { unique: true });
    });
  }

  getQuestions(collection: string) {
    return this.db.getAll('questions').then((data) => {
      return data;
    }, (error) => {
      console.log(error);
    });
  }

  insertQuestion(collection: string, data: any) {
    this.getbyQuestion(data.question).then(res => {
      if (res === undefined) {
        data.count = 1;
        this.db.add(collection, data).then(() => {
        }, (error) => {
          console.log(error);
        });
      } else {
        data = res;
        data.count++;
        data.time = new Date();
        this.db.update(collection, data).then(() => {
        }, (error) => {
          console.log(error);
        });
      }
    });
  }

  getbyQuestion(question: string) {
    return new Promise((resolve, reject) => {
      this.db.getByIndex('questions', 'question', question).then((data) => {
        resolve(data);
      }, (error) => {
        console.log(error);
      });
    });
  }
}



