import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
// import { Http, Response, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { IpInfo } from '../ip-info';

import 'rxjs/add/observable/of';

@Injectable()
export class ConnectApiService {
  connectURL = this.IpInfo.ipDetail;
  pushURL = this.connectURL + 'questions';
  options;
  constructor(private http: Http, private IpInfo: IpInfo) {
  }

  connectToAPI(apiParam: any) {
    let length = 0;
    length = apiParam.length;
    function onlyUnique(value, index, self) {
      return self.indexOf(value) === index;
    }
    const unique = apiParam.filter(onlyUnique);
    const url = this.connectURL + 'modality?fetchAll=false&';
    let name;
    let value;
    let query: string = '';
    let newstr = '';
    let all = ['all regions', 'all modalities', 'all modality', 'all region', 'all component', 'all customer name', 'allproduct', 'allproductdescription'];

    for (let i = 0; i < length; i++) {
      name = apiParam[i]['paramName'];
      value = apiParam[i]['paramValue'];
      query = name.concat('=', value, '&', query);
    }
    for (let i = 0; i < all.length; i++) {
      query = query.replace(all[i], '');
    }
    const apiConnectionString = url + query;
    console.log(apiConnectionString);
    return this.http
      .get(apiConnectionString)
      .map(apiResponse => apiResponse.json());
  }

  pushToAPI(history) {
    const bodyParam = { 'ssoId': 1, 'userId': 1, 'question': history };
    this.http.post(this.pushURL, bodyParam).subscribe();
  }

  getFromAPI() {
    return this.http.get(this.pushURL)
      .map(apiHistory => apiHistory.json());
  }

  connectToApi7(question, fromUser) {
    let apiConnectionString = this.connectURL + 'modality2?question=' + question + '&searchFilter=' + fromUser;
    return this.http
      .get(apiConnectionString)
      .map(apiResponse => apiResponse.json());
  }
}



