  // ON INITIALIZATION
  ngOnInit() {
    // this.openSnackBar();
    this.ConnectApiService.getFromAPI();
    const today = new Date();
    const curHr = today.getHours();
    if (curHr < 12) {
      this.greetingMessage = 'Good Morning ';
    } else if (curHr < 18) {
      this.greetingMessage = 'Good Afternoon ';
    } else {
      this.greetingMessage = 'Good Evening ';
    }
    this.fromUser = '';
    this.talks = '';
    this.recognition = new ((<any>window).SpeechRecognition || (<any>window)
      .webkitSpeechRecognition || (<any>window).mozSpeechRecognition || (<any>window).msSpeechRecognition)();
    this.recognition.lang = 'en-US';
    this.recognition.maxAlternatives = 1;
    this.synth = window.speechSynthesis;
    this.recognition.interimResults = true;
    this.recognition.continuous = false;
    let mapCount = [];
    const reducer = (accumulator, currentValue) => accumulator + currentValue;
    const apiConnectionString = this.connectURL + 'modality?fetchAll=true&limit=16000&question=2';
    this.http
      .get(apiConnectionString)
      .map(compResponse => compResponse.json())
      .subscribe(data => {
        mapCount = data.data;
        mapCount.map(i => i.systemCoverageLevelWarrantyc).filter((i, index, arr) => arr.indexOf(i) === index).map(i => {
          this.ibCount.push(mapCount.filter(a => a.systemCoverageLevelWarrantyc === i).map(x => x.ibCount).reduce(reducer));
          this.modality.push(i);
        });
        mapCount.map(i => i.systemCoverageLevelWarrantyc).filter((i, index, arr) => arr.indexOf(i) === index).map(i => {
          this.pieChart.push({
            name: i,
            y: mapCount.filter(a => a.systemCoverageLevelWarrantyc === i).map(x => x.ibCount / 1000).reduce(reducer)
          });
        });

        this.loadChart(this.pieChart);
      },
        error => {
          this.snackToggle = true;
          this.snackBarError = 'Cannot connect to API';
          this.disableToggle = true;
          setTimeout(() => {
            this.snackToggle = false;
          }, 5000);
        });
  }

